      </svg>
  .admin-main.admin-docs(v-if="chosenSection == 'docs'")
    .docs-container
      form.docs-block(action="#")
        .docs-block__title   
        div
          .form-block.docs-block__block.docs-block__block__calendar
            label.form-label(@click.prevent="showCalendar()")  
            input.form-input(type="text", id="orders-date", v-mask="'##.##.####'", v-model.trim="inputDate", @focus="showCalendar()", @change="checkInput()")
            FunctionalCalendar.calendar.account-form__calendar(v-model="calendarDate", :configs="calendarConfig")
          button.form-submit(type="submit") 
      form.docs-block(action="#")
        .docs-block__title    
        div
          .select-container
            v-select.select(v-model="selectedMonth", label="name", index="name", :options="months", :clearable="false", :searchable="false")
              template(v-slot:option="option")
                span.select-option {{ option.name }}
          button.form-submit(type="submit") 
  .admin-main.admin-users(v-if="chosenSection == 'users'")
    form.users-container(action="#")
      .users-title  
      table.users-table
        tr.users-table__header
          th.name   
          th.email 
          th.role 
          th.status   
          th.limit 
          th(colspan="3")
        tr.users-table__line
          td.name  
          td.email blablabla@smartworld.tem
          td.role
            .select-container
              v-select.select(v-model="selectedRole", label="name", index="name", :options="roles", :clearable="false", :searchable="false")
                template(v-slot:option="option")
                  span.select-option {{ option.name }}
          td.status
            .select-container
              v-select.select(v-model="selectedStatus", label="name", index="name", :options="statuses", :clearable="false", :searchable="false")
                template(v-slot:option="option")
                  span.select-option {{ option.name }}
          td.limit
            input.form-input(type="text")
          td.no-order.form-block.account-form__block.account-form__block__checkbox
            input.form-input.account-form__checkbox(type="checkbox", id="account-checkbox", @change="toggleCalendar()")
            label.form-label(for="account-checkbox")  
          td.no-order.form-block.account-form__block.account-form__block__calendar
            label.form-label(@click.prevent="showCalendar()")    
            .inputs-container
              input.form-input(type="text", id="account-date-start", v-mask="'##.##.####'", v-model.trim="inputsDates.start", @focus="showCalendar()", @change="checkInputs()")
              .account-form__separator
              input.form-input(type="text", id="account-date-end", v-mask="'##.##.####'", v-model.trim="inputsDates.end", @focus="showCalendar()", @change="checkInputs()")
            FunctionalCalendar.calendar.account-form__calendar(v-model="calendarDates", :configs="calendarConfig2")
          td.delete
            button.btn 
      .users-btn +
      .users-bottom
        .form-block
          label.form-label(for="users-limit")    
          .form-block__line
            input.form-input(type="text")
            span 
        button.form-submit(type="submit")  
</template>

<script>
export default {
  data() {
    return {
      chosenSection: 'docs',
      calendarConfig: {
        isDatePicker: true,
        dateFormat: 'dd.mm.yyyy',
        disabledDayNames: [''],
        disabledDates: ['afterToday'],
        monthNames: ['', '', '', '', '', '', '', '', '', '', '', ''],
        shortMonthNames: ['', '', '', '', '', '', '', '', '', '', '', ''],
        limits: { min: this.getLimitDates().min, max: this.getLimitDates().max },
        applyStylesheet: false
      },
      calendarConfig2: {
        isDateRange: true,
        dateFormat: 'dd.mm.yyyy',
        disabledDayNames: [''],
        disabledDates: ['afterToday'],
        monthNames: ['', '', '', '', '', '', '', '', '', '', '', ''],
        shortMonthNames: ['', '', '', '', '', '', '', '', '', '', '', ''],
        limits: { min: this.getLimitDates().min, max: this.getLimitDates().max },
        applyStylesheet: false
      },
      inputDate: '',
      calendarDate: { selectedDate: '' },
      selectedMonth: ' ',
      selectedRole: '',
      roles: ['', ''],
      selectedStatus: '',
      statuses: [' ', ''],
      calendarDates: {
        dateRange: {
          start: {
            date: false
          },
          end: {
            date: false
          }
        }
      },
      inputsDates: {
        start: '',
        end: ''
      },
      isChoosingDate: false
    }
  },
  methods: {
    getLimitDates() {
      const date = new Date()
      return { min: `${date.getDate()}.${date.getMonth()}.${date.getFullYear()}`, max: `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}` }
    },
    showCalendar() {
      const calendar = document.querySelector('.account-form__calendar')
      calendar.classList.add('account-form__calendar_active')
      this.hideCalendar()
    },
    hideCalendar() {
      const calendar = document.querySelector('.account-form__calendar')
      const self = this
      function hideOnClickOutside(e) {
        let a = e.target
        let parentsFlag = false
        while (a) {
          if (a == calendar) {
            parentsFlag = true
            break
          }
          a = a.parentNode
        }
        if(calendar.classList.contains('account-form__calendar_active') && !parentsFlag && !e.target.parentNode.classList.contains('docs-block__block__calendar') && !e.target.classList.contains('docs-block__block__calendar')) {
          calendar.classList.remove('account-form__calendar_active')
          self.isChoosingDate = false
          document.removeEventListener('click', hideOnClickOutside)
        }
      }
      document.addEventListener('click', hideOnClickOutside)
    },
    checkInput() {
      const input = document.getElementById('orders-date')
      if (input.value.length == 10)
        this.calendarDate.selectedDate = this.formatDateCalendar(input.value)
      else
        this.calendarDate.selectedDate = ''
    },
    formatDateInputs(dateStr) {
      let dateArr = dateStr.split('.')
      if (dateArr[0].length < 2)
        dateArr[0] = '0' + dateArr[0]
      if (dateArr[1].length < 2)
        dateArr[1] = '0' + dateArr[1]
      return dateArr.join('.')
    },
    formatDateCalendar(dateStr) {
      let dateArr = dateStr.split('.')
      if (dateArr[0].length == 2 && dateArr[0][0] == '0')
        dateArr[0] = dateArr[0][1]
      if (dateArr[1].length == 2 && dateArr[1][0] == '0')
        dateArr[1] = dateArr[1][1]
      return dateArr.join('.')
    }
  },
  computed: {
    months() {
      let months = []
      const nextMonthDay = new Date()
      const startYear = nextMonthDay.getFullYear()
      const startMonth = nextMonthDay.getMonth()
      for (let i = 0; i < 12; i++) {
        let currentYear, currentMonth
        if (startMonth - i >= 0) {
          currentYear = startYear
          currentMonth = startMonth - i
        } else {
          currentYear = startYear - 1
          currentMonth = 12 + startMonth - i
        }
        let month
        switch (currentMonth) {
          case 0:
            month = ''
            break;
          case 1:
            month = ''
            break;
          case 2:
            month = ''
            break;
          case 3:
            month = ''
            break;
          case 4:
            month = ''
            break;
          case 5:
            month = ''
            break;
          case 6:
            month = ''
            break;
          case 7:
            month = ''
            break;
          case 8:
            month = ''
            break;
          case 9:
            month = ''
            break;
          case 10:
            month = ''
            break;
          case 11:
            month = ''
            break;
          default:
            break;
        }
        months.push({ name: month + ' ' + currentYear })
      }
      return months
    }
  },
  watch: {
    calendarDate: {
      handler (value) {
        value = { selectedDate:  value.selectedDate }
        if (value.selectedDate == '')
          this.inputDate = ''
        else
          this.inputDate = this.formatDateInputs(value.selectedDate)
      },
      deep: true
    },
  }
}
</script>

<style scoped lang="sass">
@import "../assets/sass/vars"

.admin
  display: flex
  justify-content: space-between
  align-items: stretch
  width: 100%
  min-height: 100vh
  &-sidebar
    flex-basis: 75px
    min-height: 100vh
    background-color: $c-dark
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4)
    &__btn
      height: 145px
      padding: 22px
      transition: 0.2s
      svg path
        transition: 0.2s
      &:hover
        background-color: $c-middle
        svg path
          fill: $c-dark
      &_active
        background-color: $c-middle
        svg path
          fill: $c-dark
  &-main
    flex-grow: 1
    display: flex
    justify-content: center
    align-items: center
    min-height: 100vh
  .docs
    &-container
      display: flex
      justify-content: center
      align-items: stretch
    &-block
      display: flex
      justify-content: space-between
      align-items: center
      flex-direction: column
      width: 360px
      padding: 35px 55px 60px 55px
      background-color: $c-light
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25)
      &:first-child
        margin-right: 90px
      &__title
        font-weight: 500
        font-size: 24px
        line-height: 28px
        text-align: center
        margin-bottom: 80px
      .form-block
        margin-bottom: 75px
      .form-label, .form-input
        display: block
        width: 220px
      .form-submit
        width: 220px
      .select
        width: 220px
        &-container
          margin-bottom: 75px
  .account-form
    &__calendar
      display: none
      position: absolute
      top: calc(100% + 15px)
      left: 50%
      margin-left: -154px
      margin-bottom: 25px
      &_active
        display: block
  .users
    &-container
      flex-basis: 1300px
      padding: 30px 35px
      background-color: $c-light
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25)
    &-title
      font-weight: 500
      font-size: 24px
      line-height: 28px
      margin-bottom: 40px
    &-table
      width: 100%
      margin-bottom: 17px
      &__header
        border-bottom: 1px solid lighten($c-dark, 40)
        th
          padding-bottom: 15px
          font-size: 12px
          font-weight: normal
          color: lighten($c-dark, 40)
          text-align: center
      .name
        width: 170px
      .email
        width: 140px
      .role
        width: 140px
      .status
        width: 140px
      .limit
        width: 70px
      .account-form__block__checkbox
        width: 140px
      .account-form__block__calendar
        width: 210px
      .delete
        width: 110px
      &__line
        td
          padding: 14px 10px
          border-bottom: 1px solid $c-middle
        .name
          padding-left: 0
          font-weight: bold
          font-size: 13px
        .email
          font-size: 12px
        .select
          width: 125px
          height: 33px
          background-color: $c-light
          font-size: 12px
          margin: 0 auto
        .limit
          .form-input
            text-align: center
        .account-form__block__checkbox
          padding-right: 4px
          .form-label
            max-width: 140px
            font-weight: 500
            font-size: 12px
            color: $c-dark
            text-transform: none
          .account-form__checkbox
            position: absolute
            z-index: -1
            opacity: 0
          .account-form__checkbox + label
            position: relative
            padding: 0 0 0 26px
            cursor: pointer
          .account-form__checkbox + label:before
            content: ''
            position: absolute
            top: 50%
            left: 0
            margin-top: -11px
            width: 18px
            height: 18px
            border: 1px solid #2c3e50
            background: transparent
            transition: 0.1s
          .account-form__checkbox + label:after
            content: ''
            position: absolute
            top: 50%
            left: 4px
            width: 14px
            height: 14px
            margin-top: -8px
            background: url(../assets/img/tick.svg) center no-repeat
            background-size: contain
            opacity: 0
            transition: 0.1s
          .account-form__checkbox:checked + label:after
            opacity: 1
        .account-form__block__calendar
          padding-left: 4px
          .form-label
            font-size: 12px
            text-transform: none
          .inputs-container
            display: flex
            justify-content: space-between
            align-items: center
          .form-input
            width: 92px
          .account-form__separator
            width: 15px
            height: 2px
            background-color: $c-dark
            margin: 0 10px
        .delete
          padding-right: 0
          .btn
            width: 100%
            padding: 10px
            font-size: 13px
            font-weight: 500
        .select
          &-container
            margin-bottom: 0
    &-btn
      display: flex
      justify-content: center
      align-items: center
      width: 33px
      height: 33px
      border: 2px solid $c-dark
      border-radius: 50%
      font-weight: 500
      font-size: 18px
      margin: 0 auto 25px auto
      cursor: pointer
      transition: 0.2s
      &:hover
        transform: scale(1.3)
    &-bottom
      display: flex
      justify-content: space-between
      align-items: flex-end
      .form-block
        flex-basis: 300px
        margin-bottom: 0
        &__line
          display: flex
          align-items: center
          .form-input
            width: 170px
          span
            font-weight: 500
            font-size: 18px
            margin-left: 7px
      .form-submit
        flex-basis: 220px
        padding: 20px 0
</style>